import React from 'react';
import logo_png from '../images/logo.png';
import logo_webp from '../images/logo.webp';
import niser_logo_light_png from '../images/niser-logo-light.png';
import niser_logo_light_webp from '../images/niser-logo-light.webp';
import './Partners.css';

function Partners() {
    return (
        <div className='partners' >
            <hr />
            <h2 className='partners_heading' >Our Partners</h2> <hr />
            <div className="partnerlinks">
                <div className="partner_1">
                    <a href="https://www.niser.ac.in/~smishra/" >
                        <picture>
                                <source id="connect-logo" type="image/webp" srcset={logo_webp} />
                                <img alt='' id="connect-logo" src={logo_png} />
                        </picture>
                    </a>
                </div>
                <div className="partner_2">
                    <a href="http://www.niser.ac.in" >
                        <picture>
                            <source id="connect-niser-logo" type="image/webp" srcset={niser_logo_light_webp} />
                            <img alt='' id="connect-niser-logo" src={niser_logo_light_png} />
                        </picture></a>
                </div>
                <div className="partner_3">
                    <a href='https://www.niser.ac.in/scps/' >
                        <strong>SCPS, NISER</strong>
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Partners
